@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,700);
* {
  margin: 0;
  padding: 0; }

html {
  box-sizing: border-box;
  font-size: 63.5%; }

body {
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif;
  background-color: #F9FAFE;
  width: 100%;
  color: #181818; }

.icon {
