.searchInput {
  width: 100%;
  margin: 3rem 0 5rem;
  position: relative;
  letter-spacing: 1px;
  display: flex;
  justify-content: center;
  flex-direction: column; }
  @media (min-width: 480px) {
    .searchInput {
      font-size: 1.2rem; } }
  @media (min-width: 1024px) {
    .searchInput {
      align-items: center;
      margin-top: 1rem; } }
  .searchInput form {
    display: flex;
    justify-content: center; }
    @media (min-width: 1024px) {
      .searchInput form {
        width: 50%; } }
  .searchInput_input {
    font-size: 2rem;
    padding: 1rem;
    width: 100%;
    border: .5px solid #eeeeee; }
    @media (min-width: 480px) {
      .searchInput_input {
        width: 70%;
        border-radius: .5rem 0 0 .5rem; } }
    .searchInput_input::-webkit-input-placeholder {
      opacity: .5;
      font-size: 1.5rem; }
    .searchInput_input::-moz-placeholder {
      opacity: .5;
      font-size: 1.5rem; }
    .searchInput_input:-ms-input-placeholder {
      opacity: .5;
      font-size: 1.5rem; }
    .searchInput_input::-ms-input-placeholder {
      opacity: .5;
      font-size: 1.5rem; }
    .searchInput_input::placeholder {
      opacity: .5;
      font-size: 1.5rem; }
    .searchInput_input:focus {
      outline: none; }
  .searchInput_btn {
