    transition: all 0.7s ease-in-out;
    min-height: 2rem; }
    @media (min-width: 480px) {
      .searchInput_error {
        text-align: center;
        position: static; } }

.player {
  margin-top: 5rem;
  margin-bottom: 2rem; }
  @media (min-width: 480px) {
    .player {
      width: 90%;
      margin: 4rem auto; } }
  @media (min-width: 1024px) {
    .player {
      width: 60%; } }
  .player_title {
    margin-top: 2rem;
    font-size: 2rem;
    padding-left: 2rem; }

.filesDirectory {
  display: flex;
  flex-direction: column;
  margin-top: 2rem; }
  @media (min-width: 480px) {
    .filesDirectory {
      padding: 0 2rem; } }
  @media (min-width: 1024px) {
    .filesDirectory {
      width: 60%;
      margin: 3rem auto 0; } }
  .filesDirectory_placeholder {
    text-align: center;
    font-weight: bold;
    letter-spacing: 1.5px;
    opacity: .7;
    margin: 3rem 0; }

.fileSearch {
  min-height: 4rem;
  margin: 1rem 0;
  display: flex;
  padding: 0 1rem; }
  @media (min-width: 480px) {
    .fileSearch {
      width: 50%;
      margin-left: auto; } }
  @media (min-width: 1024px) {
    .fileSearch {
      width: 30%;
      margin-right: 20%; } }
  .fileSearch_input {
    padding: 1rem;
    border: 1px solid rgba(56, 69, 103, 0.2);
    flex: 1 1;
    opacity: 0;
    border-radius: 5px;
    -webkit-animation: fadeIn 0.5s 0.2s forwards;
    animation: fadeIn 0.5s 0.2s forwards; }
  .fileSearch_btn {
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10%;
