    border: 1px solid rgba(56, 69, 103, 0.2);
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
    border-radius: 5px; }
    .fileSearch_btn-icon {
      fill: black;
      width: 2rem; }

.active_btn {
  border: none; }

.active_title {
  color: #28AFB0; }

@keyFrames fadeIn {
  to {
    opacity: 1; } }

.file {
  border-radius: 5px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1rem; }
  .file_image {
    position: relative;
    width: 45%;
    flex-basis: 50%;
    cursor: pointer; }
    @media (min-width: 480px) {
      .file_image {
        flex-basis: 30%; } }
    @media (min-width: 1024px) {
      .file_image {
        flex-basis: 30%; } }
    @media (min-width: 480px) and (max-width: 815px) and (orientation: landscape) {
      .file_image {
        flex-basis: 32%; } }
    .file_image-preview {
      width: 100%; }
    .file_image-duration {
      background-color: rgba(24, 24, 24, 0.6);
      color: #ffffff;
      padding: .3rem;
      position: absolute;
      bottom: 4.5%;
      right: 1%;
      border-radius: 3px;
      font-size: 1.3rem; }
      @media (max-width: 769px) and (min-width: 480px) {
        .file_image-duration {
          bottom: 4.5%; } }
  .file_data {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    padding: 1rem .5rem; }
    .file_data-name {
      font-weight: bold;
      font-size: 1.2rem;
      cursor: pointer; }
      @media (min-width: 480px) {
        .file_data-name {
          font-size: 1.6rem; } }
    .file_data-link {
      font-size: 1.2rem;
      opacity: .8;
      margin-top: auto;
      -webkit-transition: color 0.5s ease-in-out;
      transition: color 0.5s ease-in-out;
      color: #181818; }
      @media (min-width: 480px) {
        .file_data-link {
          margin: 1rem 0; } }
    .file_data-status {
      font-size: 1.3rem;
      margin-top: auto; }
      @media (min-width: 480px) {
        .file_data-status {
          margin: auto 0; } }
    .file_data-type {
      font-size: 1.2rem;
      margin-top: .7rem; }
      @media (min-width: 480px) {
        .file_data-type {
          margin-top: 1rem; } }
  .file form {
    width: 100%;
    display: flex; }
  .file_edit-input {
    flex: 1 1;
    padding: 1rem;
    letter-spacing: 1px;
    border: 1px solid rgba(0, 0, 0, 0.2); }
    .file_edit-input:focus {
      outline: none; }
  .file_edit-btn {
    padding: 1rem;
    border: none;
    background-color: #384567;
    color: #ffffff;
    letter-spacing: 3px; }
    .file_edit-btn:focus {
      outline: none; }

@media (min-width: 1024px) {
  .fileControls {
    flex-basis: 20%; } }

.commonControls {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-around; }
  .commonControls span {
    background-color: rgba(34, 174, 209, 0.3);
    padding: .5rem;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    cursor: pointer;
    -webkit-transition: background-color 0.2s ease-in-out;
    transition: background-color 0.2s ease-in-out; }
    .commonControls span:hover {
      background-color: transparent; }
    .commonControls span svg {
      fill: rgba(34, 174, 209, 0.8); }

.mobileControls {
  position: relative; }
  .mobileControls_menu {
    position: absolute;
    left: -650%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: #ffffff;
    height: 20vh;
    padding: 1rem;
    font-weight: 500;
    box-shadow: 0 0 3.5px rgba(0, 0, 0, 0.25);
    width: 30vw;
    z-index: 2;
    opacity: 0;
    -webkit-animation: fadeIn 0.2s forwards;
    animation: fadeIn 0.2s forwards; }
    .mobileControls_menu span {
      margin: .5rem 0; }
    @media (min-width: 480px) and (max-width: 815px) and (orientation: landscape) {
      .mobileControls_menu {
        height: 30vh;
        width: 20vw;
        left: -700%; } }

@-webkit-keyframes fadeIn {
  to {
    opacity: 1; } }

@keyframes fadeIn {
  to {
    opacity: 1; } }

